import React, { useEffect, useRef } from "react";
import "./embeddingview.css";
import { loadJSONData } from "../../helperfunctions/HelperFunctions";
import * as d3 from "d3";

const width = 500;
const height = 230;
const margin = { top: 30, right: 10, bottom: 10, left: 10 };
// const k =
//   (height - margin.left - margin.right) / (width - margin.top - margin.bottom);
const strokeWidthNormal = 5;
const strokeWidthHover = 12;
const strokeOpacity = 0.6;

const toolTip = document.createElement("div");
toolTip.className = "hoveToolTip";
toolTip.style.display = "none";
const toolTipImg = document.createElement("img");
toolTipImg.className = "tooltipimg";
toolTipImg.id = "tooltipimage";
const toolTipLabel = document.createElement("div");
// toolTipLabel.innerHTML = "bingoooo";
toolTipLabel.className = "tooltiplabel";
toolTip.appendChild(toolTipLabel);
toolTip.appendChild(toolTipImg);

let toolTipImgBasePath = "";

const zoom = d3
  .zoom()
  .extent([
    [0, 0],
    [width, height],
  ])
  .scaleExtent([1, 8]);

function updateAxes(data) {
  const x = d3
    .scaleLinear()
    .domain(d3.extent(data, (d) => d.x))
    .range([margin.left, width - margin.right]);
  // console.log(x(23));
  const y = d3
    .scaleLinear()
    .domain(d3.extent(data, (d) => d.y))
    .range([margin.top, height - margin.bottom]);

  const z = d3
    .scaleOrdinal()
    .domain(data.map((d) => d.class))
    .range(d3.schemeCategory10);
  return { x: x, y: y, z: z };
}

function drawGraph(data) {
  const { x, y, z } = updateAxes(data);

  zoom.on("zoom", zoomed);

  const svg = d3
    .select("div.embeddingchart")
    .append("svg")
    // .attr("preserveAspectRatio", "xMinYMin meet")
    .attr("viewBox", [0, 0, width, height])
    .attr("preserveAspectRatio", "none")
    .attr("stroke-linecap", "round");

  const pathGroup = svg.append("g").attr("class", "scattersvg");
  pathGroup
    .selectAll("path")
    .data(data)
    .join("path")
    .attr("d", (d) => `M${x(d.x)},${y(d.y)}h0`)
    .attr("stroke", (d) => z(d.class))
    .attr("stroke-opacity", strokeOpacity)
    .attr("stroke-width", 5);

  function zoomed({ transform }) {
    pathGroup.attr("transform", transform);
  }
  svg.call(zoom).call(zoom.transform, d3.zoomIdentity);
}

function updateGraph(data) {
  const { x, y, z } = updateAxes(data);
  const svg = d3.select(".scattersvg");

  svg.transition().duration(950).attr("transform", d3.zoomIdentity);

  svg
    .selectAll("path")
    .data(data)
    .join("path")
    .on("mouseover", handleMouseOver)
    .on("mouseout", handleMouseOut)
    .transition()
    .duration(750)
    .attr("d", (d) => `M${x(d.x)},${y(d.y)}h0`)
    .attr("stroke", (d) => z(d.class))
    .attr("stroke-opacity", strokeOpacity)
    .attr("stroke-width", strokeWidthNormal)
    .attr("id", (d, i) => i);

  const legendBox = document.getElementById("legendbox");
  legendBox.innerHTML = "";

  for (const row of z.domain()) {
    const legendRow = document.createElement("div");
    const legendText = document.createElement("span");
    const legendCircle = document.createElement("div");
    legendRow.className = "iblock mr10";
    legendCircle.className = "legendcircle";
    legendCircle.style.backgroundColor = z(row);
    legendText.className = "legendtext";
    legendText.innerHTML = row;
    legendRow.appendChild(legendCircle);
    legendRow.appendChild(legendText);
    legendBox.appendChild(legendRow);
  }
}

function handleMouseOver(e, d) {
  // console.log(d3.select(".scattersvg").selectAll("path").nodes());
  // const nodes = d3.select(".scattersvg").selectAll("path").nodes();
  // const i = nodes.indexOf(d3.select(e.currentTarget));

  toolTipImg.src =
    toolTipImgBasePath + "/" + d3.select(e.target).attr("id") + ".jpg";
  toolTip.style.display = "block";
  toolTipLabel.innerHTML = d.class;
  d3.select(e.target)
    .attr("stroke-opacity", 1)
    .attr("stroke-width", strokeWidthHover)
    .attr("mouseevent", "none");
}

function handleMouseOut(e) {
  toolTip.style.display = "none";

  d3.select(e.target)
    .attr("stroke-opacity", 0.6)
    .attr("stroke-width", strokeWidthNormal);
}

export default function EmbeddingView(props) {
  const selections = props.selections;
  // const show = selections.show;
  // const config = selections.config;
  // const layers = props.models[config.getter.selectedModel].layers;
  // const selectedLayer = layers[config.getter.selectedLayer];

  // const [umapArray, setUmapArray] = useState(null);
  // console.log(selections.classlist[selections.dataset.name]);
  const scoreData = useRef(null);

  const umapPath =
    selections.basePath +
    "/assets/semsearch/umap/" +
    selections.dataset.name +
    "/" +
    selections.model.name +
    "/" +
    selections.layer.name +
    ".json";

  toolTipImgBasePath =
    selections.basePath +
    "/assets/semsearch/datasets/" +
    selections.dataset.name +
    "/";

  useEffect(() => {
    // console.log("json reloaded");
    let loadedJSON = loadJSONData(umapPath);
    loadedJSON.then(function (data) {
      if (data) {
        // console.log(data.slice(0, 3));
        // setUmapArray(data);
        updateGraph(data);
        scoreData.current = data;
        // drawGraph(data);
      }
    });
  }, [umapPath]);

  useEffect(() => {
    drawGraph([]);
    toolTipImgBasePath =
      "/assets/semsearch/datasets/" + selections.dataset.name;
    toolTipImg.src = "/assets/semsearch/datasets/iconic200/187.jpg";
    document.getElementById("embeddingchart").prepend(toolTip);
  }, [selections.dataset.name]);

  return (
    <div id="embeddingview" className="mt10 embeddingbox ">
      <div className="">
        <div className="w100 p10  legendbox" id="legendbox">
          {" "}
          Legend{" "}
        </div>
        <div
          id="embeddingchart"
          className=" w100 positionrelative   embeddingchart lightgreyhighlight"
        ></div>
      </div>
    </div>
  );
}
