import React, { useEffect } from "react";
import "./embedding.css";
import EmbeddingView from "./embeddingview/EmbeddingView";
import Bar from "./embeddingview/Bar";

export default function EmbeddingEx(props) {
  //load useful data

  const modelViewDetails = props.modelViewDetails;
  const selections = props.selections;

  useEffect(() => {
    document.title = `ConvNet Playground | Embeddings`;
  }, []);

  return (
    <div>
      <div className="flex ">
        <div className="flexfull">
          <div className="  mynotif positionrelative h100 lh10 p10 lightbluehightlight   ">
            {/* <span className="boldtext ">Explore Model Embeddings</span> */}
            {/* <br /> */}
            Embeddings are learned representations that hold semantic meaning
            for a specific set of tasks. Similar images are assigned embeddings
            that lie closer in embedding space and dissimilar images are farther
            apart.
          </div>
        </div>
      </div>

      <Bar
        models={modelViewDetails.models}
        datasets={modelViewDetails.datasets}
        metrics={modelViewDetails.metrics}
        dimreduction={modelViewDetails.dimreduction}
        selections={selections}
      ></Bar>
      <EmbeddingView
        models={modelViewDetails.models}
        datasets={modelViewDetails.datasets}
        selections={selections}
      ></EmbeddingView>

      <br />
      <br />
      <br />
    </div>
  );
}
