import React from "react";
import {
  FileUploaderButton,
  FileUploaderItem,
  // FileUploaderDropContainer,
  Loading,
} from "carbon-components-react";
import { useState } from "react";
import { uploadFile } from "../../helperfunctions/HelperFunctions";
import UploadSimilarityView from "./UploadSimilarityView";

export default function UploadButtonView(props) {
  const maxFileSize = 3;
  const [uploadedFile, setUploadedFile] = useState(null);
  const [searchResult, setSearchResult] = useState(null);
  const [fetchingSimilar, setFetchingSimilar] = useState(false);
  const selections = props.selections;
  // const searchUrl= selections.basePath + "/search"
  const searchUrl = "http://localhost:5003/search";

  function fetchSimilar(url, data) {
    setFetchingSimilar(true);
    const formData = new FormData();
    formData.append("file", data);
    formData.append("dataset", selections.dataset.name);
    let loadedJSON = uploadFile(url, formData);
    loadedJSON.then(function (data) {
      if (data) {
        setSearchResult(data);
        setFetchingSimilar(false);
      }
    });
  }

  function processFile(e) {
    const file = e.target.files[0];
    const fileData = {
      name: file.name,
      filesize: file.size / (1024 * 1024),
      size: file.size,
      type: file.type,
      valid: file.size / (1024 * 1024) < maxFileSize,
      file: file.file,
    };
    if (fileData.valid) {
      fetchSimilar(searchUrl, file);
    }
    const uploadimg = document.getElementById("uploadedimage");
    uploadimg.src = URL.createObjectURL(e.target.files[0]);
    setUploadedFile(fileData);
  }

  function clickDeleteFile(e) {
    console.log(e.target.parentNode.parentNode);
  }

  const fileList = uploadedFile ? (
    <FileUploaderItem
      key={"fileitem"}
      name={uploadedFile.name}
      status={"edit"}
      errorSubject={
        "File Too Large (" + uploadedFile.filesize.toFixed(1) + "mb)"
      }
      errorBody={"File must be less than " + maxFileSize + "mb."}
      invalid={!uploadedFile.valid}
      onDelete={clickDeleteFile}
    ></FileUploaderItem>
  ) : null;

  return (
    <>
      {fetchingSimilar && <Loading />}

      <div className="mb10 flex">
        <div>
          <img
            alt=""
            id="uploadedimage"
            className={
              "mainsimilarityimage mr10 rad4 " +
              (uploadedFile ? " " : "displaynone")
            }
          ></img>
        </div>

        <div className="mr10">
          <div className="boldtext mb10"> Upload a file </div>
          <div className=" mb10">
            {"only .jpg and .png files at " + maxFileSize + "mb or less"}
          </div>
          <FileUploaderButton
            labelText="Upload"
            // labelDescription={"only .jpg and .png files at " + maxFileSize + "mb or less"}
            name="file uploader"
            multiple={false}
            accept={["image/jpeg", "image/png"]}
            onChange={processFile}
          />
          <div className="mt10"> {fileList}</div>
        </div>
        {/* <div>
          <FileUploaderDropContainer
            labelText="Drag and drop files here or click to upload"
            multiple={false}
            onChange={processFile}
          />
        </div> */}
      </div>

      {/* <div className="underline mb10"></div> */}
      <UploadSimilarityView
        similarityArray={searchResult}
        selections={props.selections}
        selectedImage={0}
      />
    </>
  );
}
