import { Select, SelectItem } from "carbon-components-react";
import React from "react";

export default function Bar(props) {
  const selections = props.selections;
  const config = selections.config;
  const layers = props.models[config.getter.selectedModel].layers;

  function datasetChange(e) {
    config.setter.selectedDataset(e.target.selectedIndex);
  }

  function modelChange(e) {
    config.setter.selectedModel(e.target.selectedIndex);
  }
  function layerChange(e) {
    config.setter.selectedLayer(e.target.selectedIndex);
  }

  const datasetList = props.datasets.map((data, i) => {
    return (
      <SelectItem
        value={data.name}
        text={data.name}
        key={"datasetrow" + i}
      ></SelectItem>
    );
  });

  const modelList = props.models.map((data, i) => {
    return (
      <SelectItem
        value={data.name}
        text={data.name}
        key={"datasetrow" + i}
      ></SelectItem>
    );
  });

  const layerList = layers.map((data, i) => {
    return (
      <SelectItem
        value={data.layer_index + ":" + data.name}
        text={data.layer_index + ":" + data.name}
        key={"datasetrow" + i}
      ></SelectItem>
    );
  });
 
  const datestImagePath =
    selections.basePath + "/assets/semsearch/images/" + selections.dataset.icon;

  const modelImagePath =
    selections.basePath +
    "/assets/models/" +
    selections.model.name +
    "/" +
    selections.model.layers[selections.model.layers.length - 1].name +
    "/0.jpg";

  const layerImagePath =
    selections.basePath +
    "/assets/models/" +
    selections.model.name +
    "/" +
    selections.layer.name +
    "/0.jpg";

  return (
    <div className="pt10 pl10 pr10 pb5 confbarsmall ">
      <div>
        <div className="mr10 mb5 iblock">
          <img
            alt=""
            src={datestImagePath}
            className="  datasetboxsmall floatleft iblock"
          ></img>
          <Select 
            defaultValue={selections.dataset.name}
            style={{ width: "100px" }}
            className="pl10"
            onChange={datasetChange}
            id="datasetdropdown"
            labelText="Dataset"
          >
            {datasetList}
          </Select>
        </div>

        <div className="iblock mb5 mr10">
          <img
            alt=""
            src={modelImagePath}
            className="  datasetboxsmall floatleft iblock"
          ></img>

          <Select
            defaultValue={selections.model.name}
            className="pl10"
            style={{ width: "160px" }}
            onChange={modelChange}
            id="modeldropdown"
            labelText="Model"
          >
            {modelList}
          </Select>
        </div>
        <div className="iblock mb5 mr10">
          <img
            alt=""
            src={layerImagePath}
            className="  datasetboxsmall floatleft iblock"
          ></img>

          <Select
          defaultValue={selections.layer.layer_index + ":" + selections.layer.name}
            style={{ width: "180px" }}
            className="pl10"
            onChange={layerChange}
            id="layerddropdown"
            labelText="Layer"
          >
            {layerList}
          </Select>
        </div>
      </div>
    </div>
  );
}
