import React from "react";
// import UploadButtonView

export default function UploadSimilarityView(props) {
  const similarImages = props.similarityArray;

  const selections = props.selections;

  const imageBasePath =
    selections.basePath +
    "/assets/semsearch/datasets/" +
    selections.dataset.name +
    "/";

  const similarImagesList = ((similarImages || []).ids || []).map((alldata) => {
    let imagePath = imageBasePath + alldata;
    return (
      <div
        key={alldata + "winper"}
        className="iblock similarityfullbox mr5 mb5 positionrelative"
      >
        <img
          key={alldata + "image" + alldata}
          onClick={() => props.setSelectedImage(alldata)}
          src={imagePath}
          alt=""
          className={"simiimage simcomp clickable rad2 "}
          indexvalue={alldata}
        />
      </div>
    );
  });

  return (
    <div
      style={{
        display: props.similarityArray ? "" : "none",
      }}
      className=" pt10 pb5 pl10 pr10 "
    >
      <div className=" ">{similarImagesList}</div>
    </div>
  );
}
