import React, { useEffect } from "react";
import { Toggle } from "carbon-components-react";
import "./semanticex.css";
import DatasetView from "./datasetview/DatasetView";
import {
  abbreviateString,
  shuffleData,
} from "../helperfunctions/HelperFunctions";
import ConfigView from "./configview/ConfigView";

export default function SemanticEx(props) {
  const selections = props.selections;
  const showAdvanced = selections.show.getter.advanced;
  const setAdvancedDrawer = selections.show.setter.advancedDrawer;
  const setShowAdvanced = selections.show.setter.advanced;
  const selectedModelImagePath =
    selections.basePath +
    "/assets/models/" +
    selections.model.name +
    "/" +
    selections.layer.name +
    "/0.jpg";

  useEffect(() => {
    document.title = `ConvNet Playground | Semantic Search Explorer`;
  }, []);
  return (
    <div>
      <div className="flex ">
        <div className="flexfull">
          <div className="  mynotif   p10 lightbluehightlight maxh16  ">
            {
              <div className=" floatright  lightgreyhighlight ml10 pl10 pr10 pb5 ">
                <div className="boldtext  iblock ">
                  <Toggle
                    id="advancedoptionstoggle"
                    className="smalldesc boldtext mr10"
                    labelA="Off"
                    labelB="On"
                    defaultToggled={showAdvanced}
                    // onChange action('onChange'),
                    onToggle={() => {
                      if (!showAdvanced) {
                        setAdvancedDrawer(true);
                      }
                      setShowAdvanced(!showAdvanced);
                    }}
                  ></Toggle>
                </div>

                <div className="mediumdesc boldtext pt2 pb3">
                  Advanced Options
                </div>
              </div>
            }
            {/* <span className="boldtext mb5">How Does It Work?</span>
            <br /> */}
            <div className="floatleft h100 borderright pr10 mb3 mr10">
              <img
                className="introicon rad2 floatleft  mr10"
                src={selectedModelImagePath}
                alt=""
              />
              <div className="iblock ">
                <div className="   ">
                  <div className="smalldesc pb3  uppercase">
                    Dataset :{" "}
                    <span className="boldtext">{selections.dataset.name}</span>
                  </div>
                  <div className="pb3 smalldesc uppercase">
                    Model :{" "}
                    <span className="boldtext">{selections.model.name} </span>
                    {/* <span className="smalldesc">
                      | {makeFriendly(selections.model.modelparameters)}{" "}
                      parameters
                    </span> */}
                  </div>
                  <div className="pb3 smalldesc uppercase">
                    Layer :{" "}
                    <span className="boldtext">
                      {abbreviateString(selections.layer.name, 12)}{" "}
                    </span>
                    {/* <span className="smalldesc">
                      | {makeFriendly(selections.layer.modelparameters)}{" "}
                      parameters
                    </span> */}
                  </div>
                  <div className="smalldesc  uppercase">
                    Metric :{" "}
                    <span className="boldtext">{selections.metric}</span>
                  </div>
                </div>
              </div>
            </div>
            <span className="lh10 mb10">
              When you select an image (by clicking it), a neural network model
              [
              <span className="uppercase boldtext">
                {" "}
                {selections.model.name}
              </span>{" "}
              ]<span className="italics"> looks </span> at the content of all
              images in our dataset and identifies the{" "}
              <strong> top {selections.topSimilar} </strong> most similar images
              to the selected image. Click advanced options to select a
              different dataset, model (or intermediate model) and distance
              metric used for semantic search.
            </span>
          </div>
        </div>
      </div>

      {selections.show.getter.advanced && (
        <ConfigView
          models={props.modelViewDetails.models}
          datasets={props.modelViewDetails.datasets}
          metrics={props.modelViewDetails.metrics}
          selections={selections}
          showMetrics={true}
        ></ConfigView>
      )}

      <DatasetView
        data={props.datasetContent}
        dataRandom={shuffleData(props.datasetContent, props.datasetInfo)}
        meta={props.datasetInfo}
        selections={selections}
      ></DatasetView>
      <br />
      <br />
    </div>
  );
}
